@import "variables";
body{
  overflow-x: hidden;
}
.gradient{
  background: $gradient;
}
.txt-white{
  color: white;
}
.padding-xs{
  padding:5px;
}
.margin-top-lg{
  margin-top: 20px;
}
.margin-bottom-lg{
  margin-bottom: 20px;
}
.margin-left-auto{
  margin-left: auto;
}
.margin-right-auto{
  margin-right: auto;
}
.inline-block{
  display: inline-block;
}
.border{
  border-style: inset;
}
.rounded{
  border-radius: 50%;
}
.circle{
  width: 150px;
  height:150px;
}
.center-block{
  margin-left: auto;
  margin-right: auto;
}
.no-bullets{
  list-style-type: none;
  padding-left:0;
}
.circle-margin{
  margin-top: 35px;
}
.relative{
  position: relative;
}
.absolute{
  position: absolute;
}
.special-bullets{
  li {
    display: block;
  }

  li:before {
    /*Using a Bootstrap glyphicon as the bullet point*/
    content: "\e080";
    font-family: 'Glyphicons Halflings';
    font-size: 9px;
    float: left;
    margin-top: 4px;
    margin-left: -17px;
    color: #CCCCCC;
  }
}
.tree-roots{
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("/Images/tree-roots.jpg");
  box-shadow: 8px 8px 10px #aaa;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
}
.tree-1{
  background-image:linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/Images/tree-with-roots.jpg");
  box-shadow: 8px 8px 10px #aaa;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  max-width: 1330px;
}
.leaf{
  background-image: url("/Images/leaf.png");
  background-size: cover;
  background-position: center;
}
.nav-pills > li > a{
  color: white;
  font-size: 10pt;
}
.nav-pills > li > a:hover{
  background-color:green;
}
.nav-pills > li.active > a, .nav-pills > li.active > a:hover, .nav-pills > li.active > a:focus {
  color:white;
  background-color: green;
}
.code-background{
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url("/Images/jquery.png");
}
.underline{
  text-decoration: underline;
}
.shadow{
  text-shadow: 2px 2px 4px #000;
}
.box-shadow{
  box-shadow: 8px 8px 10px #aaa;
}